.card {
    background-color: white;
    width: 50%;
    margin: auto;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    margin-bottom: 4px;
}
.color{
    width: 25%;
    background-color: black;
    padding: 1rem;
    color: white;
    font-weight: 800;
    letter-spacing: 2px;
    margin-bottom: 4px;

}
.details {
    width: 100%;
    background-color: white;
    padding: 1rem;
    letter-spacing: 2px;
    margin-bottom: 4px;
}

.price {
    width: 25%;
    background-color: black;
    padding: 1rem;
    color: white;
    font-weight: 800;
    letter-spacing: 2px;
    margin-bottom: 4px;
    align-self: flex-end;
}